














































var graceChecker = require("../../../graceUI/graceChecker.js");
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {}
	},
	computed: mapState(['school', 'user']),
	methods: {
		formSubmit: function(e) {
			//定义表单规则
			var rule = [{
					name: "market_name",
					checkType: "string",
					checkRule: "1,8",
					errorMsg: "超市名称应为1-8个字符"
				},
				{
					name: "dorm_tower",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "宿舍楼不能为空"
				},
				{
					name: "dorm_num",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "宿舍号不能为空"
				}
			];
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				formData.user_id = this.user.id;
				formData.phone = this.user.phone;
				formData.token = this.user.token;
				formData.market_school = this.school.title;
				uni.request({
					url: this.GLOBAL.serverSrc + 'market/index/regmarket',
					method: 'POST',
					data: formData,
					success: res => {
						if (res.data.status === 200) {
							uni.navigateTo({
								url: './verifyIdCard'
							});
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							});
						}
					},
					fail: (e) => {
						this.GLOBAL.requestFail(e);
					}
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		}
	}
}
